	import {  
        mapMutations  
    } from 'vuex';
	import interfaces from '../../utils/interface.js';



	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: true,
				isReg : false,//是否注册后登录
			}
		},
		onLoad(option){
			this.isReg = option.isReg;
			console.log(this.isReg)
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key; //监控输入
				this[key] = e.detail.value; //登录号码
				this.logining = false;
			},
			setPhone(){
				//修改密码
				uni.navigateTo({
					url: '../set/base/setPhone'
				})
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				uni.navigateTo({
					url : './register'
				})
			},
			async toLogin(){
				// this.logining = true;
				const {mobile, password} = this;
				
				if(!mobile){
					uni.showToast({
						icon:"none",
						title:"请输入手机号"
					})
					return;
				}
				else if(!password){
					uni.showToast({
						icon:"none",
						title:"请输入密码"
					})
					return;
				}
				else {
					this.request({
						url: interfaces.login,
						method: "POST",
						data: {
							tell : this.mobile,
							password : this.password
						},
						success: ((res) => {
							
							if(res.error_code == 0){
								let session_id = res.session;
								const userInfo = {				
										id: 1,
										mobile: this.mobile,
										nickname: `砍砍用户${this.mobile}`,
										
										portrait: '../../static/user_img.png'
								}

								uni.removeStorageSync('sessionid')
								uni.setStorageSync("sessionid", `PHPSESSID=${session_id}`); //储存服务器返回session_id,解决session会话结束

								this.login(userInfo)
								if(this.isReg){//如果是注册后登录
									uni.switchTab({
										url: '../home/home'
									})
								}else{ 
									uni.navigateBack()
								}
								// uni.navigateBack()
								
							}else{
								uni.showToast({
									icon:'none',
									title: '账号或密码错误'
								})
							}			
						})
					});





























					// uni.navigateBack();  
				}
			}
		},

	}
